/*
*  mutations
*  state
*  state{
*     openId:'',
*     token:'',
*  },
*  mutations
*  mutations:{
*     setOpenId:(state,data)=>{state.openId=data},
*     setToken:(state,data)=>{state.token=data}
*  },
*  mutation
*
*  vue
*
*  computed:{
*   ...mapState([
*     'openId','token'
*   ])
*  },
*
*  methods::{
*   ...mapMutations([
*     'setOpenId','setToken'
*   ])
*  }
*
*/
export function getDefaultMutations(state) {
    let mutations = {};
    for (let key in state)
        mutations[`set${key.charAt(0).toUpperCase()}${key.substring(1, key.length)}`] = (state, data) => state[key] = data;
    return mutations;
}

/*
*  getters
*  state
*  state{
*     openId:'',
*     token:'',
*  },
*  mutations
*  getters:{
*     openId:(state)=>state.openId,
*     token:(state)=>state.token
*  },
*  getters
*
*  vue
*
*  computed:{
*   ...mapGetters([
*     'openId','token'
*   ])
*  },
*
*/
export function getDefaultGetters(state) {
    let getters = {};
    for (let key in state)
        getters[key] = (state) => state[key];
    return getters;
}

